.App {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	text-align: center;
}

body {
	margin: 0;
	padding: 0;
	background: linear-gradient(180deg, #0d0b44 0%, #2b0f77 50%, #5a0f91 100%);
	font-family: Arial, sans-serif;
	min-height: 100vh;
	overflow: hidden;
}

.welcome {
	position: relative;
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
}

/* üåå –¶–µ–Ω—Ç—Ä–∞–ª—å–Ω—ã–π –±–ª–æ–∫ */
.floating-box {
	position: relative;
	width: 700px;
	max-width: 95%;
	height: 95%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	background: radial-gradient(
		ellipse at top,
		rgba(60, 20, 90, 0.9),
		rgba(10, 5, 25, 0.95)
	);

	border-radius: 24px;
	overflow: hidden;

	box-shadow: 0 0 30px rgba(255, 136, 0, 0.6), 0 0 60px rgba(255, 68, 0, 0.5),
		inset 0 0 40px rgba(90, 15, 145, 0.4);

	padding: 30px 20px;
	animation: floatBoxGlow 6s infinite alternate ease-in-out;
}

.play-now-btn:disabled {
	opacity: 0.4;
	cursor: not-allowed;
	box-shadow: none;
	background: #555;
	border: 2px solid #777;
}

/* ‚ú® –°–ª–æ–π –∑–≤—ë–∑–¥–æ—á–µ–∫ */
.floating-box::before,
.floating-box::after {
	content: '';
	position: absolute;
	inset: 0;
	background-image: radial-gradient(2px 2px at 20px 30px, #fff, transparent),
		radial-gradient(1.5px 1.5px at 80px 120px, #ffddaa, transparent),
		radial-gradient(2px 2px at 200px 80px, #aaddff, transparent),
		radial-gradient(1.2px 1.2px at 300px 200px, #fff, transparent),
		radial-gradient(1.8px 1.8px at 500px 150px, #ffeedd, transparent),
		radial-gradient(2px 2px at 650px 250px, #ffffff, transparent);
	background-repeat: repeat;
	background-size: 700px 700px;
	animation: drift 60s linear infinite;
	opacity: 0.6;
	z-index: 0;
	pointer-events: none;
}

.floating-box::after {
	animation: drift 100s linear infinite reverse, twinkle 3s infinite alternate;
	opacity: 0.9;
	z-index: 0;
	pointer-events: none;
}

/* üëæ –õ–æ–≥–æ—Ç–∏–ø */
.bot-logo {
	width: 250px;
	height: 250px;
	background-image: url('./assets/forBot.png');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	user-select: none;
	pointer-events: none;
	margin-bottom: 50px;
}

/* ‚ú® –ê–Ω–∏–º–∞—Ü–∏–∏ */
@keyframes floatBoxGlow {
	0% {
		box-shadow: 0 0 30px rgba(255, 136, 0, 0.5),
			0 0 60px rgba(255, 68, 0, 0.4),
			inset 0 0 40px rgba(90, 15, 145, 0.4);
	}
	100% {
		box-shadow: 0 0 40px rgba(255, 200, 100, 0.7),
			0 0 80px rgba(255, 80, 0, 0.6),
			inset 0 0 60px rgba(120, 30, 200, 0.5);
	}
}

@keyframes drift {
	0% {
		background-position: 0 0;
	}
	100% {
		background-position: 700px 700px;
	}
}

@keyframes twinkle {
	0% {
		opacity: 0.3;
	}
	100% {
		opacity: 1;
	}
}

/* üìù –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.title {
	font-size: 2rem;
	font-weight: bold;
	text-align: center;

	background: linear-gradient(90deg, #ffeb3b, #ff9800, #ff5722, #e53935);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

	text-shadow: 0 0 10px rgba(255, 193, 7, 0.8),
		0 0 20px rgba(255, 87, 34, 0.8), 0 0 30px rgba(229, 57, 53, 0.7);

	margin-bottom: 40px;
	margin-top: -10px;
	position: relative;
	z-index: 1;
}

/* –ò–Ω–ø—É—Ç */
.input-id {
	width: 80%;
	max-width: 300px;
	padding: 12px 16px;
	margin-bottom: 20px;
	font-size: 1rem;
	text-align: center;
	border-radius: 12px;
	border: 2px solid #ffba66;
	background: rgba(255, 255, 255, 0.08);
	color: #fff;
	outline: none;
	z-index: 1;
	position: relative;
}
.input-id:focus {
	box-shadow: 0 0 10px rgba(255, 186, 102, 0.7);
}

/* –°–ª–∞–π–¥–µ—Ä */
.bomb-selector {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 15px 0 25px;
	width: 100%;
	gap: 15px;
	position: relative;
	z-index: 1;
}

.bomb-slider {
	-webkit-appearance: none;
	width: 180px;
	height: 6px;
	background: #ff8c00;
	border-radius: 4px;
	outline: none;
	cursor: pointer;
}
.bomb-slider::-webkit-slider-thumb {
	-webkit-appearance: none;
	width: 22px;
	height: 22px;
	border-radius: 50%;
	background: #fff;
	border: 2px solid #ff4500;
	cursor: pointer;
	box-shadow: 0 0 10px rgba(255, 140, 0, 0.8);
}
.bomb-slider::-moz-range-thumb {
	width: 22px;
	height: 22px;
	border-radius: 50%;
	background: #fff;
	border: 2px solid #ff4500;
	cursor: pointer;
	box-shadow: 0 0 10px rgba(255, 140, 0, 0.8);
}

.bomb-count-label {
	color: #ffba66;
	font-weight: bold;
	font-size: 1.2rem;
	text-shadow: 0 0 6px rgba(255, 136, 0, 0.7);
	z-index: 1;
	position: relative;
}

/* –ú–∏–Ω–∏-–ø–æ–ª–µ */
.mini-field {
	display: grid;
	grid-template-columns: repeat(5, 60px);
	grid-gap: 8px;
	justify-content: center;
	align-items: center;
	margin-bottom: 20px;
	z-index: 1;
	position: relative;
}

.cell {
	width: 60px;
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.8rem;
	background: rgba(255, 255, 255, 0.08);
	border-radius: 8px;
	box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.15);
}
.icon {
	display: block;
	font-size: 1.6rem;
	user-select: none;
	pointer-events: none;
}

/* –ö–Ω–æ–ø–∫–∞ Play */
.play-now-btn {
	width: 200px;
	height: 80px;
	margin-top: 40px;

	font-size: 1.5rem;
	font-weight: bold;
	color: #fff;
	text-transform: uppercase;
	cursor: pointer;

	background: linear-gradient(180deg, #ff8c00 0%, #ff4500 100%);
	border: 3px solid #ffba66;
	border-radius: 999px;

	box-shadow: 0 0 20px rgba(255, 136, 0, 0.8), 0 0 40px rgba(255, 68, 0, 0.8);

	transition: transform 0.2s, box-shadow 0.2s;
	z-index: 1;
	position: relative;
}
.play-now-btn:hover {
	transform: scale(1.05);
	box-shadow: 0 0 30px rgba(255, 136, 0, 1), 0 0 60px rgba(255, 68, 0, 1);
}
.play-now-btn:active {
	transform: scale(0.95);
}

/* üì± –ê–¥–∞–ø—Ç–∏–≤ ‚Äî —Ç–µ–ª–µ—Ñ–æ–Ω—ã */
@media (max-width: 768px) {
	.floating-box {
		width: 95%;
		height: auto;
		min-height: 90%;
		max-height: 95vh;
		overflow-y: auto;
		border-radius: 16px;
		padding: 20px 15px;
	}

	.bot-logo {
		width: 120px;
		height: 120px;
		margin-bottom: 20px;
	}

	.input-id {
		width: 100%;
		max-width: 260px;
		padding: 10px;
		font-size: 0.9rem;
	}

	.bomb-slider {
		width: 140px;
	}

	.mini-field {
		grid-template-columns: repeat(5, 45px);
		grid-gap: 6px;
		margin-bottom: 15px;
	}
	.cell {
		width: 45px;
		height: 45px;
		font-size: 1.3rem;
	}

	.play-now-btn {
		width: 160px;
		height: 60px;
		font-size: 1.2rem;
		margin-top: auto; /* –ø—Ä–∏–∂–∏–º–∞–µ—Ç—Å—è –∫ –Ω–∏–∑—É */
	}
}
